// App.js
import React, { Component } from 'react';
import { db } from './firebase';
import Form from './Form';
import Calendar from './Calendar';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      uid: null,
      crops: [],
      loading: true
    };
  }

  componentDidMount() {
    // Wait for Android to send UID
    if (window.firebaseUID) {
      this.initWithUID(window.firebaseUID);
    } else {
      document.addEventListener("firebase-uid-ready", () => {
        this.initWithUID(window.firebaseUID);
      });
    }
  }

  initWithUID(uid) {
    this.setState({ uid }, () => {
      this.fetchCrops();
    });
  }

  fetchCrops() {
    const { uid } = this.state;

    if (!uid) return;

    db.collection("crops")
      .where("userId", "==", uid)
      .get()
      .then(snapshot => {
        const cropsList = snapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));
        this.setState({ crops: cropsList, loading: false });
      })
      .catch(error => console.error("Error fetching crops:", error));
  }

  addCrop() {
    const { uid } = this.state;
    const newCrop = { 
      name: "", 
      seasons: [{ start: "", end: "" }],
      userId: uid
    };

    db.collection("crops").add(newCrop)
      .then(docRef => {
        this.setState({
          crops: [...this.state.crops, { ...newCrop, id: docRef.id }]
        });
      });
  }

  deleteCrop(index) {
    const crop = this.state.crops[index];
    if (crop.id) db.collection("crops").doc(crop.id).delete();
    this.setState(prev => ({
      crops: prev.crops.filter((_, i) => i !== index)
    }));
  }

  updateCrop(index, updated) {
    const crops = [...this.state.crops];
    const crop = { ...crops[index], ...updated };

    crops[index] = crop;
    this.setState({ crops });

    if (crop.id) db.collection("crops").doc(crop.id).update(crop);
  }

  addSeasonToCrop(index) {
    const crops = [...this.state.crops];
    crops[index].seasons.push({ start: "", end: "" });
    this.setState({ crops });
  }

  saveAllCrops() {
    this.state.crops.forEach(crop => {
      db.collection("crops").doc(crop.id).update(crop);
    });
    alert("All crops saved!");
  }

  render() {
    if (this.state.loading) {
      return <p>Loading...</p>;
    }

    return (
      <div id="app">
        <div className="app-bar">
          <h1>Harvest Calendar</h1>
        </div>

        <header>
          <p>Plan pineapple farming with equal harvest distribution.</p>
        </header>

        <div id="form-container">
          <div id="instructions">
            <h3>How to use</h3>
            <p>Add crops and schedule the seasons.</p>
            <button className="btn btn-secondary" onClick={() => this.addCrop()}>Add Crop</button>
            <button className="btn btn-primary" onClick={() => this.saveAllCrops()}>Save</button>
          </div>

          <Form
            id="form"
            crops={this.state.crops}
            updateCrop={this.updateCrop.bind(this)}
            deleteCrop={this.deleteCrop.bind(this)}
            addSeasonToCrop={this.addSeasonToCrop.bind(this)}
          />
        </div>

        <Calendar crops={this.state.crops} />
      </div>
    );
  }
}

export default App;



